import React, { Component } from 'react';
import fetch from 'node-fetch';

import Page from '../components/page';
import AtomicCard from '../components/atomic-card';

import { GridList } from 'rmwc/GridList'
import { Typography } from '../node_modules/rmwc';

import '../styles/news.scss';

export default class News extends Component {
    static async getInitialProps() {
        var news = undefined;
        
        await fetch(`https://atomic-api.herokuapp.com/news`)
          .then(response => response.json())
          .then(json => {
            if ('error' in json) {
              //stats = {displayName: "Unknown"};
            } else {
              news = json;
            }
          })
          .catch(ex => {
            //stats = {displayName: "Unknown"};
          })
    
        return {news};
    }

    render() {
        return (
            <Page>
                <AtomicCard className="atomic-news" title="News" titleSize="headline3" titleColor="var(--mdc-theme-primary)" outlineColor="var(--mdc-theme-primary)" backgroundColor="var(--drawer-color)" width="calc(100% - 8px)" maxWidth="1150px">
                    <GridList>
                        <div className="atomic-news-tile" style={{ textAlign: "justify", paddingTop: "25px" }}>
                            <img style={{ float: "left" }} src={this.props.news[0].image} />
                            <Typography style={{ color: "var(--mdc-theme-primary)", float: "left", paddingTop: "20px" }} use="headline3">{this.props.news[0].title}</Typography>
                            <br />
                            <Typography style={{ color: "white" }} use="headline4">{this.props.news[0].description}</Typography>
                        </div>
                    </GridList>
                </AtomicCard>
            </Page>
        )
    }
}